@import "../../style.scss";

.comments {
    @include themify($themes) {
        img{
            width: 40px;height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
        .write{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin: 20px 0px ;

            input{
                flex: 5;
                padding: 10px;
                border: 1px solid themed("border");
                background-color: transparent;
                color: themed("textColor");
            }
            button{
                border: none;
                background-color: #5271ff;
                color: white;
                padding: 10px;
                border-radius: 3px;
                cursor: pointer;
            }

        }
        .comment{
            margin: 30px 0px;
            display: flex;
            justify-content: space-between;
            gap: 10px;
            .info{
                flex: 5;
                display: flex;
                flex-direction: column;
                gap: 10px;
                align-items: flex-start;
                span{
                    color: themed("textColor");
                    font-weight: 500;
                }
                p{
                    margin-top: -9px;
                    color: themed("textColorsoft");
                }
            }
            .date{
                flex: 1;
                align-self: center;
                color: gray;
                font-size: 12px;
            }
        }
    }
}